.slide7_title {
    font-family: $CRUni;
    font-style: normal;
    font-weight: normal;
    font-size: 72px;
    line-height: 87px;
    color: $textwhite;
}
.slide7__flex {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.slide7_textbox {
    display: flex;
    flex-direction: column;
    align-items: center;  
    width: 80%;
    margin-top: 10%;
}
.slide7_textbox_text {
    font-family: $CRGA;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    color: $textwhite;
    width: 50%;
    text-align: left;
}
.slide7_textbox_form {
    font-family: $CRGA;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
    color: $textblack;
    background-color: $bgwhite;
    width: 50%;
    text-align: center;
    margin-bottom: 5%;
    box-shadow: none;
    border:none;
}
.slide7_textbox_button {
    font-family: $CRGA;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 50px; 
    color: $textwhite;
    background-color: $bgviol;
    border: 1px solid $bgwhite;
    width: 30%;
}
